$ns: "fdt-";
@import 'style/global.scss';

.CartPage {
    &__row {
        align-items: center;

        height: calc(100vh - 84px);
    }

    &__container {
        height: calc(100vh - 84px);
    }

    &__text {
        text-align: center;
    }

    &__overlay {
        position: fixed;
        z-index: 3;
        top: 0;
        left: 0;

        width: 100vw;
        height: 100vh;

        background-color: rgba(0, 0, 0, .5);

        transition: opacity .4s ease-out;
    }

    &__visible {
        position: relative;
        z-index: 34;
    }

    &__icon {
        width: scale-w(56);
        height: scale-h(86);
        margin-bottom: scale-h(20);
    }
}
